.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(0, 0, 0, 0.70);
        backdrop-filter: blur(5px);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 37.5em;
        max-width: calc(100% - 1em);
        max-height: calc(100 * var(--vh));
        position: relative;
        z-index: 3;
        overflow-y: auto;
        padding: 3.125em 5em;
        &:before,
        &:after{
            content: '';
            position: absolute;
            top: 0;
            height: 100%;
            width: 1px;
            background-color: #e7e7e7;
        }
        &:before{
            left: 5em;
            transform: translateX(-50%);
        }
        &:after{
            right: 5em;
            transform: translateX(50%);
        }
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: 1.25em;
        right: 1.25em;
        background-color: transparent;
        width: 1.875em;
        z-index: 2;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__top{
        @include grid100gap(.625em);
        text-align: center;
    }
    &__title{
        font-size: 2em;
        line-height: 140%;
    }
}

.modal-form{
    margin-top: 1.875em;
    &__nav,
    &__field{
        width: calc(100% + 10em);
        margin-left: -5em;
        padding: 0 5em;
        border-top: $border3;
    }
    &__nav{
        border-bottom: $border3;
        padding: 0 calc(5em - 1px);
        position: relative;
        z-index: 2;
    }
    &__btn{
        width: 100%;
    }
    .fg{
        margin-bottom: -1px;
        &:before,
        &:after{
            width: 0.6875em;
            height: 0.6875em;
            z-index: 2;
            top: 0;
        }
        &:before{
            left: 0;
        }
        &:after{
            right: 0;
        }
    }
    &__confirm{
        margin-top: 1.25em;
        text-align: center;
        a{
            white-space: nowrap;
            color: $secondary;
            text-decoration: underline;
        }
    }
}


@media screen and (max-width: 1031px){
    .modal{
        &__dialog{
            //border-radius: 24px;
            //padding: 24px 16px;
            padding: 2em 1.25em;
            &:before{
                left: 1.25em;
            }
            &:after{
                right: 1.25em;
            }
        }
        &__close{
            top: .5em;
            right: 1.375em;
        }
        &__top{
            gap: .25em;
            .text20{
                font-size: 1em;
            }
        }
        &__title{
            font-size: 1.5em;
        }
    }

    .modal-form{
        &__nav,
        &__field{
            width: calc(100% + 2.5em);
            margin-left: -1.25em;
            padding: 0 1.25em;
        }
        &__nav{
            padding: 0 calc(1.25em - 1px);
        }
        .fg{
            &:before{

            }
        }
    }
}


